* {
  padding: 0;
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

body {
  font-family: Helvetica, Arial, sans-serif;
  background-color: #eee;

}

#root {
  --bar-width: 500px;
  --sb-progress-color: #FC561E;
  --sb-progress-bg-color: #DDD;
  --sb-progress-height: 10px;
  color: #fff;
  font: 28px/1em 'Helvetica';
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


.list {
  width: var(--bar-width);
  height: 500px;
}

