.button {
  cursor: pointer;
  padding: 20px;
  background-color: white;
  border-radius: 2px;
}

.soundcloud-player {
  overflow: visible;
  font-size: 14px;
}

.play {
  height: 25px;
  background: none;
  outline: none;
  border:none;
  width: 25px;
  cursor: pointer;
  fill: #fff;
}

.play > svg {
  fill:#fc561e;
}


#addButton {
  margin: 0px 0 10px 4px;
}

.sc-progress-container {
  background-color: var(--sb-progress-bg-color);
  width: 100%;
  height: var(--sb-progress-height);
  overflow: visible;
  cursor: pointer;
  margin: -8px 0 8px -10px;
}

.sc-progress-inner {
  background-color: var(--sb-progress-color);
  height: 100%;
  -webkit-transition: width .2s ease-in;
  -o-transition: width .2s ease-in;
  transition: width .2s ease-in;
}



